html, body {
  background-color: #f5f7fa;
  font-family: 'Montserrat', 'Open Sans', sans-serif;
  height: 100%; }

#app {
  height: 100%; }

.tl-nav-container {
  background-color: #364B59;
  height: 70px; }

.tl-nav-header {
  font-size: 30px;
  color: white;
  font-weight: 300;
  margin-left: 20px;
  line-height: 47px; }

.tl-nav-brand {
  margin-top: 5px; }

.tl-nav-right {
  text-align: right;
  margin-top: 25px; }

.tl-nav-button, .tl-nav-button-border {
  color: white;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color: transparent;
  border: none;
  font-size: 16px;
  font-weight: 100;
  font-family: 'Montserrat', 'IBM Helvetica', sans-serif;
  text-decoration: none; }

.tl-nav-button-border {
  border-right: white 2px solid; }

#header-link1, #header-link2, #header-link3 {
  border-right: white 2px solid; }

.ar-card {
  height: 410px;
  width: 90%;
  margin: 10px;
  background-color: #fff;
  box-shadow: 2px 2px 2px #CFD7D8;
  padding-left: 0px !important;
  padding-right: 0px !important; }

.ar-card-img {
  width: 100%;
  height: 195px; }

.ar-card-title-div {
  height: 56px;
  display: flex; }

.ar-card-title-text {
  font-size: 18px;
  color: #2E3133;
  padding-left: 15px;
  align-self: center; }

.ar-card-action-button {
  align-self: center;
  background: transparent;
  border: none; }

.ar-card-desc-div {
  overflow-y: hidden;
  height: 88px !important; }

.ar-asset-member-name {
  font-size: 18px;
  color: #5596e6;
  text-decoration: none;
  line-height: 22px; }

.ar-card-desc-div-expand {
  overflow-y: hidden;
  min-height: 60px !important;
  max-height: 110px !important; }

.ar-card-more-div {
  min-height: 20px;
  max-height: 70px; }

.ar-card-desc-text {
  text-overflow: ellipsis;
  overflow-y: hidden;
  padding-left: 12px;
  padding-right: 14px;
  font-size: 15px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300; }

.ar-card-button-more {
  align-self: center;
  background: transparent;
  border: none;
  font-size: 15px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  padding-left: 10px;
  color: #5596e6; }

.ar-card-button-more:hover, .ar-card-button-more:active, .ar-card-button-more:focus, .ar-card-button-more:visited {
  outline: none;
  color: #3d70b2; }

.ar-tag-flexbox {
  display: flex;
  flex-wrap: wrap;
  margin-left: 15px;
  margin-right: 15px; }

.ar-tag-flexbox-footer {
  display: flex;
  flex-wrap: wrap;
  margin-left: 15px;
  margin-right: 15px;
  min-height: 56px;
  margin-top: 5px; }

.ar-tag-div {
  color: #ffffff;
  background: linear-gradient(45deg, #3d70b2, #5596e6);
  background: #5596e6;
  background: -webkit-linear-gradient(45deg, #3d70b2, #5596e6);
  background: -o-linear-gradient(45deg, #3d70b2, #5596e6);
  background: -moz-linear-gradient(45deg, #3d70b2, #5596e6);
  border-radius: 100px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 2px;
  padding-bottom: 2px;
  margin-right: 5px;
  margin-left: 5px;
  margin-bottom: 7px;
  flex-basis: auto;
  text-align: center;
  white-space: nowrap;
  align-self: center; }

.ar-tag-text {
  font-size: 14px; }

.ar-row-margin10 {
  margin-top: 10px;
  margin-bottom: 10px; }

.ar-tab-flex-container {
  display: flex;
  margin-bottom: 10; }

.ar-tab-header {
  font-weight: 300;
  font-size: 22px;
  margin-right: 5px;
  margin-top: 0.2rem; }

.tab {
  font-family: 'Montserrat', 'Open Sans', sans-serif;
  background-color: transparent;
  font-weight: 300;
  font-size: 22px;
  border: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin-right: 5px;
  margin-left: 5px; }

.tab-active {
  font-family: 'Montserrat', 'Open Sans', sans-serif;
  background-color: transparent;
  font-weight: 300;
  font-size: 22px;
  border: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin-right: 5px;
  margin-left: 5px;
  border-bottom-width: 0.10em;
  border-bottom-style: solid;
  -webkit-border-image: -webkit-linear-gradient(45deg, #3d70b2, #5596e6);
  -moz-border-image: -moz-linear-gradient(45deg, #3d70b2, #5596e6);
  -o-border-image: -o-linear-gradient(45deg, #3d70b2, #5596e6);
  border-image: linear-gradient(45deg, #3d70b2, #5596e6);
  border-image-slice: 1;
  color: #5596e6; }

.tab:active, .tab:focus, .tab:visited {
  outline: none; }

.bx--checkbox-wrapper {
  height: 30px !important; }

.ar-checkbox-filter {
  height: 30px; }

.ar-autocomplete-suggestion-div {
  margin-right: 25%;
  margin-left: 25%; }

.ar-autocomplete-suggestion-text {
  padding-right: 20px;
  padding-left: 15px;
  font-weight: 300; }

.ar-dropdown-divider {
  display: flex; }

.ar-dropdown-divider-text {
  margin: 5px;
  margin-left: 15px;
  font-weight: 500; }

.ar-dropdown-divider-div {
  display: block;
  border-bottom: 1px solid #8C9BA5;
  flex-shrink: 0;
  flex-grow: 1;
  flex-basis: auto;
  align-self: center;
  margin-right: 10px;
  margin-left: 5px;
  margin-top: 3px; }

.ar-search-icon {
  position: relative;
  top: -35px;
  left: 95%; }



